@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
.main_header {
  margin: 10px 0 20px;
  letter-spacing: 6px;
  text-transform: capitalize;
  text-align: center;
  color: #eee;
}

.input_container {
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-around;
  align-items: center; */
  margin: 10px 0 30px;
  position: relative;
}

.input {
  border: 2px solid #222;
  border-radius: 5px;
  margin-right: auto;
  padding: 2px 10px;
  font-size: 1.2em;
  outline: none;
  max-width: 300px;
}

.empty {
  border: 2px solid red;
}

.input-btn {
  cursor: pointer;
  background: #1b3caa;
  border: 2px solid #1b3caa;
  padding: 8px 20px;
  min-width: 100px;
  border-radius: 5px;
  font-size: 1em;
  color: #eee;
  outline: none;
  transition: all 0.1s ease-in-out;
}

.input-btn:hover {
  background: #1234a5;
}

.link {
  position: absolute;
  top: 110%;
  left: -20%;
  width: 100%;
  font-size: 10px;
}

.link a {
  color: white;
  text-decoration: none;
  letter-spacing: 3px;
}

.link a:hover {
  color: #ddd;
}

.display {
  position: relative;
  min-height: 20em;
  min-width: 25em;
  background-color: /*#cadbc8;*/ whitesmoke;
  border-radius: 10px;
  /* box-shadow: inset 0px 0px 20px 10px rgba(0, 0, 0, 0.2),
    0px 0px 30px 5px rgba(0, 0, 0, 0.5); */
  box-shadow: inset rgba(50, 50, 93, 0.3) 0px 0px 100px -20px,
    rgba(0, 0, 0, 0.6) 0px 0px 60px -20px;
  padding: 10px;
}

.display h1 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

h1 .place {
  font-size: 2rem;
  margin: 5px;
}

.region h1 {
  font-size: 1rem;
  margin: 5px;
}

/* .temp_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
} */

.temp {
  font-size: 2rem;
  margin: 10px 0;
}

.condition {
  font-size: 1.5rem;
  margin: 10px auto;
}

.refresh {
  cursor: pointer;
  background: #1b3caa;
  border: 2px solid #1b3caa;
  padding: 8px 20px;
  margin: 10px;
  min-width: 100px;
  border-radius: 5px;
  font-size: 1em;
  color: #eee;
}

.refresh:hover {
  background: transparent;
  color: #222;
}

.no_data {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2.5rem;
  margin: 0;
  color: rgb(180, 33, 33);
}

@media (max-width: 700px) {
  .main_header {
    margin: 10px 0 10px;
    letter-spacing: 1px;
  }

  .input_container {
    display: block;
    width: 80%;
    margin: auto;
  }

  .input {
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    outline: none;
  }
  .input-btn {
    margin: 20px 0;
  }
  .input-btn:hover {
    background: #0d2a8b;
  }

  .link {
    top: 35%;
    left: 0%;
  }
  .display {
    min-width: 10px;
    margin: 0 30px;
    padding: 5px;
  }
}

* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
  min-height: 100vh;
}

body {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
  text-align: center;
  background: linear-gradient(rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5) 100%),
    url(/react-weather-forecast/static/media/mist-1.03478eb5.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

